/* eslint-disable */

import { gsap } from "gsap/dist/gsap";

class HeaderBig2023 {
  delay = 0;
  duration = 2;

  constructor(header) {
    this.header = header;
    this.titles = [...this.header.querySelectorAll('h2 strong span')]
    this.pattern = this.header.querySelector('svg .header-pattern');
    this.init()
  }

  init() {
    this.animation = this.animate();
    this.animation.play();
  }

  animate() {
    // rotate circle in svg
    gsap.to(this.pattern, {
      duration: 360,
      rotation: 360,
      transformOrigin: '50% 50%',
      ease: 'none',
      repeat: -1,
    })

    const tl = gsap.timeline({
      paused: true,
      repeat: -1,
    });

    let timepos = 0;

    for (let i = 0; i < this.titles.length; i++) {
      let current = i;
      let next = i + 1;

      if(i === this.titles.length - 1) {
        current = this.titles.length - 1;
        next = 0;
      }

      // let timepos = (i + 1) * this.duration + (i + 1) * this.delay;

      const t1 = timepos + this.duration / 3 * 2;
      const t2 = timepos + this.duration / 3;
      tl
        .to(this.header,
          {
            background: this.titles[next].dataset.background,
            "--color-header": this.titles[next].dataset.color,
            duration: this.duration,
            ease: "none"
          }, timepos)

      tl
        .to(this.titles[current],
          {
            opacity: 0,
            duration: this.duration / 3,
            ease: "none"
          }, t2)

      tl
        .to(this.titles[next],
          {
            opacity: 1,
            duration: this.duration / 3,
            ease: "none"
          }, t1)

      timepos += this.duration + this.delay;
    }

    return tl;
  }
}

export default HeaderBig2023;

/* eslint-enable */
